@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700&display=swap);
body {
    margin: 0;
    font-size: 15px;
}

*, *:after, *:before {
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}


.main-layout {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1.2fr 6fr; }
  .main-layout .sidebar-container {
    height: 100%; }
  .main-layout main {
    display: grid;
    grid-template-rows: 230px auto; }
    .main-layout main header {
      width: 100%; }
    .main-layout main section {
      background-color: #ffb6b0; }
  .main-layout .footer-container {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0; }
  .main-layout .minimized-banner {
    grid-template-rows: 150px auto; }

nav {
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#5444d5), color-stop(#5144d7), color-stop(#4d44d8), color-stop(#4945da), to(#4545dc));
  background-image: -webkit-linear-gradient(top, #5444d5, #5144d7, #4d44d8, #4945da, #4545dc);
  background-image: linear-gradient(to bottom, #5444d5, #5144d7, #4d44d8, #4945da, #4545dc); }
  nav .brand {
    text-align: center;
    margin: 0;
    padding: 50px 0;
    font-size: .6em;
    letter-spacing: 3px;
    color: white; }
  nav .menu {
    position: -webkit-sticky;
    position: sticky;
    top: 20px; }
    nav .menu ul {
      margin: 0;
      list-style: none;
      padding: 0; }
      nav .menu ul li {
        padding: 20px 0 20px 30px;
        opacity: .3;
        cursor: pointer;
        -webkit-transition: .5s;
        transition: .5s;
        display: grid;
        grid-template-columns: 1fr 5fr; }
        nav .menu ul li:hover {
          opacity: .8; }
        nav .menu ul li p {
          margin: 0;
          display: inline-block;
          padding-left: 6px;
          color: white;
          font-weight: 600;
          font-size: .92em; }
        nav .menu ul li .icon {
          color: white; }
      nav .menu ul .active {
        opacity: 1; }

.fade-enter {
    opacity: 0 !important;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
}
.fade-enter-active {
    opacity: 1 !important;
    -webkit-transition: opacity 700ms;
    transition: opacity 700ms;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.fade-exit {
    opacity: 1 !important;
}
.fade-exit-active {
    opacity: 0 !important;
    -webkit-transition: opacity 700ms;
    transition: opacity 700ms;
}
footer {
  box-shadow: 0px -1px 3px 0px #ebebeb;
  background-color: rgba(255, 255, 255, 0.8);
  height: 75px;
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-min-content minmax(20%, 50%) auto;
  grid-template-columns: max-content min-content minmax(20%, 50%) auto; }
  footer .now-playing {
    padding-left: 15px;
    padding-right: 4px;
    height: 100%;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 60px minmax(70px, auto); }
    footer .now-playing img {
      object-fit: cover;
      width: 50px;
      height: 50px;
      border-radius: 5px; }
    footer .now-playing .author {
      padding: 0 15px;
      justify-self: start; }
      footer .now-playing .author h3, footer .now-playing .author p {
        margin: 0; }
      footer .now-playing .author h3 {
        font-size: 1em;
        color: #666666; }
      footer .now-playing .author p {
        font-size: .88em;
        color: #9d9d9d; }
  footer .controls {
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: repeat(3, 40px); }
    footer .controls .control {
      color: #b7b7b7;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      cursor: pointer;
      -webkit-transition: .3s;
      transition: .3s; }
      footer .controls .control:hover {
        color: #2a292b; }
    footer .controls .play {
      color: #5444d5;
      -webkit-transform: scale(2);
              transform: scale(2); }
  footer .timer {
    margin-left: 10px;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 6fr 1fr;
    position: relative; }
    footer .timer .line-container {
      width: 100%;
      display: grid;
      align-items: center;
      grid-column-gap: 1px;
      position: relative; }
      footer .timer .line-container .invisible {
        background-color: transparent;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 100;
        cursor: pointer; }
      footer .timer .line-container .circle {
        width: 15px !important;
        height: 15px;
        border-radius: 50%;
        border: 2px solid white;
        background-color: #5444d5;
        -webkit-transition: .3s;
        transition: .3s;
        cursor: pointer; }
        footer .timer .line-container .circle:hover {
          background-color: #212035; }
      footer .timer .line-container .line-before, footer .timer .line-container .line-after {
        height: 4px;
        border-radius: 4px;
        opacity: .9; }
      footer .timer .line-container .line-before {
        background-color: #7058ff; }
      footer .timer .line-container .line-after {
        background-color: #d0d0e8; }
    footer .timer .time {
      color: #9a9a9a;
      font-size: .85em; }
  footer .settings {
    margin-left: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40px, 1fr));
    align-items: center;
    justify-items: center;
    position: relative; }
    footer .settings .volume-slider {
      position: absolute;
      top: -25px;
      left: -50%;
      -webkit-transform: translateX(50%);
              transform: translateX(50%); }
    footer .settings .setting {
      color: #a1a1a1;
      cursor: pointer;
      -webkit-transition: .3s;
      transition: .3s; }
      footer .settings .setting:hover {
        color: #5444d5;
        -webkit-transform: scale(1.1);
                transform: scale(1.1); }
    footer .settings .active {
      color: #5444d5; }

.volume-slider {
  background-color: white;
  border-radius: 6px;
  box-shadow: 1px 1px 5px 0 #d6d6d6;
  padding: 6px 12px;
  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  grid-gap: 8px;
  justify-items: center;
  align-items: center; }
  .volume-slider svg {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    color: #929292; }
  .volume-slider .steps {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-gap: 2px; }
    .volume-slider .steps span {
      border-radius: 3px;
      height: 4px;
      cursor: pointer; }

header {
  background-color: #ffb6b0;
  display: grid;
  align-items: center;
  padding: 0 50px;
  position: relative;
  overflow: hidden; }
  header h1 {
    justify-self: right;
    font-size: 2.3em;
    color: white; }

.some-form {
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform-origin: left top;
          transform-origin: left top;
  fill: #ffeef3;
  opacity: 0.15;
  -webkit-animation: wave 15s alternate infinite;
          animation: wave 15s alternate infinite; }

@-webkit-keyframes wave {
  from {
    opacity: .1;
    -webkit-transform: scaleX(1.9) scaleY(1);
            transform: scaleX(1.9) scaleY(1); }
  to {
    opacity: .19;
    -webkit-transform: scaleX(2.6) scaleY(1.1);
            transform: scaleX(2.6) scaleY(1.1); } }

@keyframes wave {
  from {
    opacity: .1;
    -webkit-transform: scaleX(1.9) scaleY(1);
            transform: scaleX(1.9) scaleY(1); }
  to {
    opacity: .19;
    -webkit-transform: scaleX(2.6) scaleY(1.1);
            transform: scaleX(2.6) scaleY(1.1); } }

.from-bottom-enter {
    opacity: 0 !important;
    -webkit-transform: translateY(80px);
            transform: translateY(80px);
}
.from-bottom-enter-active {
    opacity: 1 !important;
    -webkit-transition: all 700ms;
    transition: all 700ms;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.from-bottom-exit {
    opacity: 1 !important;
}
.from-bottom-exit-active {
    opacity: 0 !important;
    -webkit-transition: all 700ms;
    transition: all 700ms;
    -webkit-transform: translateY(80px);
            transform: translateY(80px);
}
.songs {
  padding: 25px 35px 100px 35px;
  width: 100%;
  position: relative;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background-color: white; }
  .songs .top, .songs .list {
    width: 80%; }
  .songs .top h2 {
    margin: 0;
    color: #313131;
    font-weight: 800;
    font-size: 1.35em;
    padding-bottom: 5px; }
  .songs .top p {
    margin: 0;
    color: #8e8e8e;
    font-size: .88em;
    font-weight: 400; }
  .songs .top .right {
    position: absolute;
    top: 35px;
    right: 14%;
    display: grid;
    grid-template-columns: -webkit-max-content repeat(2, 40px);
    grid-template-columns: max-content repeat(2, 40px);
    justify-items: center;
    color: #a5a5a5; }
    .songs .top .right p {
      font-size: .86em; }
    .songs .top .right p, .songs .top .right svg {
      cursor: pointer;
      -webkit-transition: .3s;
      transition: .3s; }
      .songs .top .right p:hover, .songs .top .right svg:hover {
        color: #5444d5; }
  .songs .list .headline, .songs .list article {
    display: grid;
    grid-template-columns: 2fr 1.5fr 0.5fr; }
  .songs .list .headline {
