    padding: 7px;
    border-bottom: 2px solid #f5f5f5;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
    opacity: .9;
    position: relative; }
    .songs .list article:hover {
      -webkit-transform: scale(1.01);
              transform: scale(1.01);
      box-shadow: 0 2px 3px 1px rgba(238, 238, 238, 0.66);
      opacity: 1; }
    .songs .list article .track {
      display: grid;
      align-items: center;
      grid-template-areas: "i t t t t t t t ." "i a a a a a a a ."; }
      .songs .list article .track img {
        grid-area: i;
        border-radius: 5px;
        width: 50px;
        height: 50px;
        object-fit: cover; }
      .songs .list article .track .title {
        grid-area: t;
        margin: 0;
        font-size: 1em;
        padding-bottom: 2px;
        color: #4b4b4b; }
      .songs .list article .track .artist {
        grid-area: a;
        margin: 0;
        font-size: .85em;
        color: #9a9a9a; }
    .songs .list article .album, .songs .list article .duration {
      font-size: .88em;
      color: grey; }
    .songs .list article .duration {
      padding-left: 3px; }
    .songs .list article .heart, .songs .list article .dots {
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      color: #bfbfbf;
      opacity: .9;
      -webkit-transition: .3s;
      transition: .3s; }
      .songs .list article .heart:hover, .songs .list article .dots:hover {
        opacity: 1;
        color: #5444d5; }
    .songs .list article .heart {
      position: absolute;
      right: -50px; }
    .songs .list article .dots {
      position: absolute;
      right: -90px; }
  .songs .list .active {
    background-color: #7058ff;
    border-radius: 5px; }
    .songs .list .active .track .title, .songs .list .active .track .artist {
      color: white; }
    .songs .list .active .album, .songs .list .active .duration {
