.volume-slider {
  background-color: white;
  border-radius: 6px;
  box-shadow: 1px 1px 5px 0 rgba(214, 214, 214, 1);
  padding: 6px 12px;

  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  grid-gap: 8px;
  justify-items: center;
  align-items: center;

  svg {
    transform: scale(.8);
    color: #929292;
  }

  .steps {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-gap: 2px;

    span {
      border-radius: 3px;
      height: 4px;
      cursor: pointer;
    }


  }

}