@import '../../assets/css/variables';

.main-layout {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1.2fr 6fr;

  .sidebar-container {
    height: 100%;
  }

  main {
    display: grid;
    grid-template-rows: 230px auto;

    header {
      width: 100%;
    }

    section {
      background-color: $bannerCreamy;

    }

  }

  .footer-container {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  .minimized-banner {
    grid-template-rows: 150px auto;
  }

}